body {
  background: #3F3351;
}

.App {
  width: 100%;
  height: 100vh;
  text-align: center;
  color: black;
}

.app_bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 40px;
}

.image {
  background: linear-gradient(0deg, rgba(25, 25, 25, 1) 0%, rgba(142, 142, 142, 1) 100%);
  max-height: 600px;
  width: auto;
}

.details_box {
  left: 2.5%;
  bottom: 15px;
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  width: 95%;
  /* margin-top: 10rem; */
  padding: 0.7rem 0;
  border: 1px solid rgba(142, 142, 142, 1);
  border-radius: 0.4rem;
}

.weather_description {
  /* top: 0;
  left: 37%; */
  /* position: relative; */
  width: auto;
  height: 20vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
}

.weather_icon {
  width: 60px;
  height: 60px;
}

.toolbar_gap {
  width: 100%;
  height: 100px;
}